body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Poppins', sans-serif;
    /* background: linear-gradient(90deg, #71b7e6, #9b59b6); */
    background-color: #00DBDE;
background-image: linear-gradient(90deg, #00DBDE 0%, #FC00FF 100%);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

i{
  cursor: pointer;
}

form {
  /* background: linear-gradient(90deg,#71b7e6, #1a3f57, #58206e); */
  background-color: #0093E9;
  background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
  
  margin-left: 35%;
  padding: 40px;
  width: 400px;
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
}
 .modal-form{
  margin-left: 30px;
}

.form-control {
  background-color: whitesmoke;
  border: none;
  border-radius: 10px;
  padding: 10px 15px;
  margin-bottom: 15px;
  font-size: 14px;
  color: #333;
  transition: 0.3s;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
}

.form-control:focus {
  outline: none;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.card{
  /* background: linear-gradient(90deg,#9a75a8, #ae4497, #58206e); */
  background-color: #FFDEE9;
background-image: linear-gradient(0deg, #FFDEE9 0%, #B5FFFC 100%);

  color:rgb(18, 1, 1);
}
.navbar{
  background: linear-gradient(90deg,#58206e, #247db7, #58206e);
}
h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 600;
}

.btn-primary {
  background-color: #3498db;
  border: none;
  font-weight: 600;
  color: white;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
}